/* Large devices (desktops, less than 1280px) */
@media (max-width: 1280px){
	h1{
		font-size: 45px;
	}

	.announcements{
		padding-bottom: 58px;
	}

	.announcements .accrodion .toggle{
		padding: 32px 0 30px 0;
	}

	.announcements .accrodion .toggle .info>span.number{
		font-size: 16px;
	}

	.announcements .accrodion .inner{
		margin-top: 40px;
	}

	.announcements .accrodion .inner__content{
		padding-bottom: 63px;
	}

	.announcements .accrodion .inner__content .announce__desc .announce__text{
		line-height: 23px;
	}
}

/* Medium devices (tablets, less than 992px) */
@media (max-width: 992px){
	.header .heading{
		grid-template-columns: 1fr 6fr 1fr;

		.nav__menu{
			justify-content: center;
		}

		.offer{
			grid-area: 2 / 1 / span 1 / span 2; 

			.offer__title{
				font-size: 45px;
				max-width: 442px;
			}
		}
	}

	.announcements>.container{
		grid-template-columns: 1fr;
	}

	.announcements .accrodion{
		grid-column: unset;

		.inner{
			margin-top: 0;
		}

		.inner__content{
			padding-bottom: 55px;
		}

		.inner__content, .toggle{
			grid-template-columns: 1fr 2fr;
		}
	}
}

/* Small devices (landscape phones, less than 768px) */
@media (max-width: 768px){
	.announcements .accrodion .inner__content, .announcements .accrodion .toggle{
		grid-template-columns: 3fr 5fr;
	}

	.announcements .accrodion .inner__content .thumb .announce__thumb {
		width: 186px;
    	height: 170px;
	}

	.announcements .accrodion .inner__content .announce__desc .announce__text{
		font-size: 9px;
		max-width: 297px;
		line-height: 18px;
	}
}

/* Extra small devices (portrait phones, less than 576px) */
@media(max-width: 576px){
	.menu_bars{
		grid-column: 3 / 4;
	}

	.header .heading .offer .offer__title{
		font-size: 20px;
	}

	.offer__btn{
		margin-top: 31px;
		margin-left: 0 !important;
	}

	.announcements .accrodion .toggle {
	    grid-template-columns: 1fr 1fr;
	    grid-template-rows: 1fr 1fr;
	}

	.announcements .accrodion .toggle .info{
		grid-column: 1/3;
	}

	.announcements .accrodion .toggle .info>span{
		width: auto;
	}

	.announcements .accrodion .toggle .info>span.time{
		margin-right: 27px;
	}

	.announcements .accrodion .toggle .info>span.number{
		margin-left: auto;
	}

	.announcements .accrodion .toggle .titles{
		grid-row: 2/3;
    	grid-column: 1/3;
	}



	.announcements .accrodion .inner__content{
		grid-template-columns: 1fr;

		.thumb{
			width: 100%;
			
			.announce__thumb{
				width: 100%;
				margin-right: 0;
				object-position: top;
			}
		}
	}

	.announcements .accrodion .inner__content .announce__desc{
		grid-column: 1/2;
		margin-top: 25px;
	}
}