// Modules
@import "modules/variables";

// Partials
@import "partials/reset";
@import "partials/buttons";
@import "partials/typography";
@import "partials/menu";

// Vendor
@import "vendor/slideout";

// Hero
.header{
	padding: 40px 0 69px 0;
  	background: url("../img/background.jpg") no-repeat center center / cover;
	
	.heading{
		display: grid;
		grid-template-columns: 2fr 8fr 2fr;

		.nav{
			margin-left: 0px;
		}

		.offer{
			grid-area: 2 / 2 / span 1 / span 1;
			
	  		&__title{
	  			max-width: 600px; 
	  			width: auto; 			
	  		}

	  		&__btn{
	  			margin-left: auto;
	  			display: block;
	  		}

	  		margin-top: 107px;

	  	}
	}
}

// Announcements
.announcements{
	background: $main_bg;
	padding: 0 0 175px 0;

	& > .container{
		display: grid;
		grid-template-columns: 2fr 8fr 2fr;
	}

	.accrodion{
		grid-column: 2/3;

		.toggle{
			display: grid;
			grid-template-columns: 1fr 1fr;

			padding: 49px 0 37px 0;
			border-bottom: 1px solid rgba(131, 131, 131, 0.6);

			position: relative;

			.info{
				grid-column: 1/2;

				& > span{
					display: inline-block;
					width: 33%;
				}

				& > span.number{
					font-size: 25px;
					font-weight: 700;
					letter-spacing: 0.07em;
				}
			}

			.titles{
				grid-column: 2/3;
				&__one{
					color: #AB4FF2;
					margin-bottom: 6px;
				}

				&__two{
					letter-spacing: 0.07em;
					line-height: 17px;
				}
			}

			.caret{
				position: absolute;
				right: 0;
				top: 50%;
				transform: translateY(-50%);
				transition: all .3s ease;
			}
		}

		.inner {
	        overflow: hidden;
	       	display: none;
	       	margin-top: 48px;

	        &__content{
	        	display: grid;
	        	grid-template-columns: 1fr 1fr;

	        	padding-bottom: 86px;
    			border-bottom: 1px solid rgba(131,131,131,0.6);

	        	.thumb{
	        		grid-column: 1/2;
	        		text-align: right;
	        		width: 100%;

	        		.announce__thumb{
	        			margin-right: 104px;
	        			width: 200px;
		        		height: 200px;
		        		object-fit: cover;
	        		}
	        	}
	        	.announce__desc{
	        		grid-column: 2/3;

	        		.announce__titles{
						margin-bottom: 17px;
	        		}

	        		.announce__text{
						line-height: 1.625em;
	        		}
	        	}
	        }
	    }
	}
}

@import "partials/responsive/general-responsive";
@import "partials/responsive/announcement-responsive";